/* 共通部分
------------------------------------------------------ */
@use "setting" as *;

html {
  box-sizing: border-box;
  font-size: 100%;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-family: $font_Josefin, Arial, $font_Hiragino, "Hiragino Sans", Meiryo, sans-serif;
  font-feature-settings: "palt";
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
  vertical-align: top;
}
