/* heading
------------------------------------------------------ */
@use "../../../global/" as *;
@use "../../../base/setting" as *;
.heading-lv2 {
  font-size: rem(40);
  line-height: 1;
  @include mq(lg) {
    font-size: rem(64);
  }
}

.heading-lv2--color--reverse {
  color: $c_white;
}

.heading-lv2__subtitle {
  display: block;
  margin-top: 10px;;
  font-size: rem(14);
  font-weight: bold;
  @include mq(lg) {
    font-size: rem(20);
  }
}