/* header
------------------------------------------------------ */
@use "../base/setting" as *;
@use "../global/mixin/breakpoint" as *;

.header {
  @include z-index(header);
}

.header__inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-right: $padding-side-sp;
  padding-left: $padding-side-sp;

  @include mq(lg) {
    max-width: $layout-width;
    padding-right: $padding-side;
    padding-left: $padding-side;
    margin: 0 auto;
  }
}
