/* コンセプト
------------------------------------------------------ */
@use "../../../base/setting" as *;
@use "../../../global/" as *;

.concept__title {
  font-size: rem(56);
  font-style: italic;
  font-weight: 700;
  color: $c_key1;
  text-align: center;

  @include mq(lg) {
    font-size: rem(108);
  }
}

.concept__title-br {
  @include mq(md) {
    display: none;
  }
}

.concept__subtitle {
  margin-bottom: 25px;
  font-weight: bold;
  text-align: center;
  letter-spacing: 0.1em;

  @include mq(lg) {
    font-size: rem(32);
  }
}

.concept__subtitle span {
  color: $c_key1;
}

.concept__text {
  margin-bottom: 20px;
  font-size: rem(15);
  line-height: 1.8;
  text-align: center;

  @include mq(lg) {
    margin-bottom: 30px;
    font-size: rem(16);
    line-height: 2;
  }
}
