/* メインビジュアル
------------------------------------------------------ */
@use "../../../base/setting" as *;

@use "../../../global/" as *;

.mv {
  position: relative;
}

.mv__inner {
  padding-right: $padding-side-sp;
  padding-bottom: 34px;
  padding-left: $padding-side-sp;

  @include mq(lg) {
    padding-bottom: 105px;
    padding-left: 12%;
  }

  @include mq(xl) {
    max-width: 1800px;
    padding-left: $padding-side;
    margin: 0 auto;
  }
}

.mv__text-box {
  @include mq(xl) {
    padding-left: 12%;
  }
}

.mv__title {
  padding: 110px 0 3px;
  font-size: min(11vw, rem(90));
  font-style: italic;
  font-weight: inherit;
  line-height: 1;
  color: $c_key3;

  @include mq(lg) {
    padding-top: 172px;
  }
}

.mv__text-color {
  background-color: $c_white;
}

.mv__text-color--accent {
  font-size: min(9vw, 85px);
  color: $c_key1;
}

.mv__subtitle {
  padding-bottom: 12px;
  font-size: min(3.5vw, 28px);
  font-weight: bold;
  letter-spacing: -1px;

  @include mq(lg) {
    padding-bottom: 25px;
  }
}

.mv__img {
  position: absolute;
  top: 0;
  right: 0;
  width: 70%;
  height: 410px;
  border-radius: 0 0 0 50px;
  animation: 1s fadeInMvImg 0.2s forwards;
  object-fit: cover;

  @include z-index(mv);

  @include mq(sm) {
    height: 410px;
  }

  @include mq(md) {
    height: 500px;
  }

  @include mq(lg) {
    height: 680px;
    border-radius: 0 0 0 90px;
  }

  @include mq(xxxl) {
    padding-right: 50px;
  }
}
