/* タブ（トレーナー）
------------------------------------------------------ */
@use "../../../global/" as *;
@use "../../../base/setting" as *;

.tab-navigation {
  padding-bottom: 20px;
}

.tab-navigation__inner {
  display: flex;
}

.tab-navigation__item {
  width: 100%;
  list-style: none;
  cursor: pointer;
}

.tab-navigation__link {
  display: block;
  font-size: rem(16);
  font-weight: bold;
  text-align: center;
  padding-bottom: 10px;
  color: $c_white;
  text-decoration: none;
  transition: .25s;
  @include mq(lg) {
    font-size: rem(18);
    padding-bottom: 20px;
  }
}

.tab-navigation__link--active {
  border-bottom: 2px solid $c_white;
}