/* タブ（トレーナー）
------------------------------------------------------ */
@use "../../../base/setting" as *;
@use "../../../global/" as *;

.tab-card {
  overflow: hidden;
  border-radius: 10px;
  background-color: $c_white;
}

.tab-card__body {
  padding: 20px;
}

.tab-card__text {
  font-size: rem(14);
  line-height: 1.6;
  @include mq(lg) {
    font-size: rem(15);
    line-height: 1.7;
  }
}