/* メディアカード
------------------------------------------------------ */
@use "../../../global/" as *;
@use "../../../base/setting" as *;

.media {
  position: relative;
  padding-bottom: 37px;

  @include mq(lg) {
    flex-direction: row;
    padding-bottom: 100px;
  }
}

.media--reverse {
  @include mq(lg) {
  display: flex;
  justify-content: right;
  }
}

.media__img-inner {
  @include mq(lg) {
    width: 70%;
  }
}

.media__img {
  width: 100%;
}

.media__body {
  position: relative;
  padding: 40px 30px 30px 30px;
  margin: -10px 20px;
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 0 20px rgb(0 0 0 / 10%);
  @include mq(lg) {
    position: absolute;
    padding: 60px;
    width: 555px;
    bottom: 90px;
    right: 18px;
  }
}


.media__body--reverse {
  @include mq(lg) {
  left: 20px;
  }
}

.media__subtitle {
  margin-bottom: 24px;
  font-size: rem(18);
  color: $c_key1;

  @include mq(lg) {
    font-size: rem(24);
  }
}

.media__subtitle--food {
  color: #00b21b;
}

.media__title {
  margin-bottom: 15px;
  font-size: rem(22);
  font-weight: bold;
  line-height: 1.5;

  @include mq(lg) {
    font-size: rem(32);
  }
}

.media__text {
  line-height: 1.7;
  font-size: rem(14);
  @include mq(lg) {
    font-size: rem(15);
    line-height: 1.8;
  }
}
