/* ボタン
------------------------------------------------------ */
@use "../../../base/setting" as *;
@use "../../../global/" as *;

.btn {
  display: inline-block;
  width: 300px;
  max-width: 100%;
  padding: 16px 10px;
  font-size: rem(13);
  font-weight: bold;
  color: $c_white;
  text-align: center;
  text-decoration: none;
  background-color: $c_key1;
  border-radius: 50px;

  @include mq(lg) {
    width: 360px;
    padding: 26px 10px;
    font-size: rem(20);
  }
}

// 色違い
.btn--color--reverse {
  color: $c_key1;
  background-color: $c_white;
}

// ヘッダーボタン
.btn--header {
  width: 186px;
  padding: 12px 10px;
  font-size: rem(12);

  @include mq(lg) {
    width: 250px;
    font-size: rem(15);
  }
}

// mvボタン
.btn--mv {
  width: 220px;
  @include mq(lg) {
    width: 370px;
  }
}