@use "../global/" as *;

// 画像パス
$imgPath: "images/";

// コンテナーの左右余白
$padding-side: 20px;
$padding-side-sp: 5%;

// ページの全体の横幅（大）
$width_lg: 1340px;

// ページの全体の横幅（標準）
$width: 1170px;

// ページの全体の横幅（中間）
//$width_md : 800px;

// ページの全体の横幅（狭い）
//$width_sm : 800px;

// レイアウト幅
$layout-width: $width + $padding-side * 2;
$layout-width_lg: $width_lg + $padding-side * 2;

//標準フォントサイズ(sp)
//$fz_pc: 14px;

//標準フォントサイズ(pc)
//$fz_pc: 16px;

// 標準font-family
// $font_serif : 'Shippori Mincho', serif;
$font_Hiragino: "Hiragino Kaku Gothic ProN";

//欧文font-family
// $font_heebo : 'Heebo', sans-serif;
// $font_roboto : 'Roboto', sans-serif;
// $font_bebas : 'Bebas Neue', cursive;
$font_Josefin: "Josefin Sans";

//白黒
$c_white: #fff;
$c_black: #141414;

//グレー
$c_gray1: #ddd;
$c_gray2: #707070;

// $c_gray2: #BEBEBE;
// $c_gray3: #707070;
// $c_gray4: #E8E8E9;
// $c_gray5: #505050;

//キーカラー
$c_key1: #eb51c2;
$c_key2: #00b21b;
$c_key3: #111;

//黄色系
// $c_yellow1: #FFD800;

//青系
// $c_blue1: #1286EA;
// $c_blue2: #3D98B4;
// $c_blue3: #BAF4FF;

//緑系
// $c_green1: #02964E;
// $c_green2: #EEF3F1;

//赤系
// $c_red1: #F7320E;
// $c_red2: #F7E6E9;
// $c_red3: #FCF2F2;

// 標準テキストカラー
$c_text: #111;

// リンクカラー
$c_link: $c_text;
$c_hover: $c_key3;
$c_active: $c_key3;

// z-index
$layer: (
  modal: 100,
  header: 20,
  tooltip: 10,
  badge: 2,
  default: 1,
  mv: -1,
);

@mixin z-index($key) {
  z-index: map-get($layer, $key);
}
